/* Dashboard.css */

body {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f5f5f5;
  }
  
  .dashboard {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }
  
  .dashboard-header {
    background-color: #4a90e2;
    color: #fff;
    padding: 1rem 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .dashboard-header h1 {
    margin: 0;
  }
  
  .dashboard-header nav a {
    color: #fff;
    text-decoration: none;
    margin-left: 1rem;
    font-weight: bold;
  }
  
  .dashboard-main {
    flex-grow: 1;
    padding: 2rem;
    background-color: #f5f5f5;
  }
  
  .dashboard-main h2 {
    margin-bottom: 1.5rem;
    color: #333;
  }
  
  .book-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 1.5rem;
  }
  
  .book-card {
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    display: flex;
    flex-direction: column;
    transition: transform 0.3s, box-shadow 0.3s;
  }
  
  .book-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 12px rgba(0, 0, 0, 0.2);
  }
  
  .book-cover {
    width: 100%;
    height: 300px;
    object-fit: cover;
  }
  
  .book-info {
    padding: 1rem;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  .book-info h3 {
    margin: 0 0 0.5rem 0;
    color: #333;
  }
  
  .book-author {
    color: #888;
    margin-bottom: 0.5rem;
  }
  
  .book-description {
    color: #555;
    margin-bottom: 1rem;
  }
  
  .book-link {
    text-decoration: none;
    color: #4a90e2;
    font-weight: bold;
    text-align: right;
  }
  
  .dashboard-footer {
    background-color: #4a90e2;
    color: #fff;
    text-align: center;
    padding: 1rem;
    margin-top: auto;
  }
  